	module.exports = {"data":{"markdownRemark":{"html":"<p>思考中央与地方关系，在纸上写：</p>\n<p>中央的权威应该从全党的权威写，在改革开放中，全党均要有权威，这个权威的集中代表是中央;</p>\n<p>中央和地方的党组织均要有权威，但是关键是中央，中央有权威了，地方就有权威了;</p>\n<p>维护中央的权威就是维护全局的权威;</p>\n<p>涉及全局的中央没有现存决策的必须请示中央;</p>\n<p>地方的重大问题的决策及时通报中央;</p>\n<p>地方在中央统一领导下发挥积极性和创造性;</p>\n<p>中央积极支持地方工作;</p>\n<p>地方党委有责任保证中央政令在本地区畅通;</p>\n<p>中央权威的本质是全党统一意志基础上的统一领导权;</p>\n<p>决策正确才能保证效率;</p>\n<p>属于政府管理的应通过民主集中制来解决，不属于的通过市场机制来解决。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0103.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0103.md","frontmatter":{"title":"6月1日 星期三"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0103.md absPath of file >>> MarkdownRemark"}}

